@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;500;700&display=swap);
*{
  margin: 0;
  font-family: 'Roboto', sans-serif;
}


.chat{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 20px;
    height: 70px;
    border-bottom: 1px solid #f9f9f9;
}

.chat__details{
    -webkit-flex: 1 1;
            flex: 1 1;
}

.chat__image{
    margin-right: 20px;
}

.chat__details>p{
    color: gray;
}

.chat__timestamp{
    color: gray;
}

a{
    text-decoration: none;
    color: inherit;
}
.chatScreen{
    
}

.chatScreen__message{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 20px;

}

.chatScreen__text{
    margin-left: 10px;
    background-color: lightgray;
    padding: 15px;
    border-radius: 20px;
}

.timestamp{
    text-align: center;
    padding: 20px;
    color: gray;
}

.chatScreen__textUser{
    margin-left: auto;
    background-color: rgb(107, 187, 219);
    padding: 15px;
    border-radius: 20px;
    color: white;
}

.chatScreen__input{
    display: -webkit-flex;
    display: flex;
    padding: 5px;
    position: fixed;
    bottom: 0;
    width: 100%;
    border-top: 1px solid lightgray;
}

.chatScreen__inputfield{
    -webkit-flex: 1 1;
            flex: 1 1;
    padding: 10px;
    border: none;
}

.chatScreen__button{
    border: none;
    margin-right: 20px;
    background-color: white;
    font-weight: bolder;
    color: palevioletred;
}
.header{
    background-color: white;
    z-index: 100;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    border-bottom: 1px solid #f9f9f9;
}
.header__logo{
    height: 40px;
     
}

.header__icon{
    padding: 20px;
}
.swipeButtons{
    position: fixed;
    bottom: 10vh;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.swipeButtons .MuiIconButton-root{
    background-color: white;
    box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.3);
}

.swipeButtons__repeat{
    color: orange !important;
}

.swipeButtons__left{
    color:palevioletred !important;
}

.swipeButtons__star{
    color: skyblue !important;
}

.swipeButtons__right{
    color: greenyellow !important;
}

.swipeButtons__lightning{
    color: gray !important;
}

.card{
    position: relative;
    width: 600px;
    padding: 20px;
    max-width: 85vw;
    height: 50vh;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
    box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.3);
}

.card>h3{
    position: absolute;
    bottom: 10px;
    color: white;
}

.tinderCards__cardContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 5vh;
}

.swipe{
    position: absolute;
}
